import React, { Component } from "react";
import styles from "../styles/css/Login.module.css";
import googlebtn from "../assets/googlebtn.png";
import kakaobtn from "../assets/kakaobtn.png";

class Login extends Component {
  //
  render() {
    return (
      <div className={styles.logincontainer}>
        <br></br>
        <img alt="" src={"img/.svg"} />
        <br></br>
        <br></br>
        <div className={styles.lh2}>      </div>
        <br></br>
        <div>
          <button
            className={styles.kakao}
            onClick={() => window.open("http://localhost:8080/oauth2/authorization/kakao")}>
            <img src={kakaobtn} width="222" alt="  " />
          </button>
        </div>
        <br></br>
        <div>
          <button
            className={styles.google}
            onClick={() =>
              window.open("http://localhost:8080/oauth2/authorization/google")
            }
          >
            <img src={googlebtn} width="222" alt="  " />
          </button>
        </div>
        <div className={styles.guideline}>
          ______________________________________________________
        </div>
        <span className={styles.guide}>   </span>
        <span className={styles.guide2}></span>
        <span className={styles.guide}>  . </span>
      </div>
    );
  }
}

export default Login;