import React, { useState, useEffect } from "react";
import axios from "axios";
import styles from "../styles/css/ItemList.module.css";
import { ListItem } from "./ListItem";
import { useParams } from "react-router";
import Pagination from "./Pagination";
import Loading from "./Loading";

//  
const truncate = (str, n) => {
  return str?.length > n ? str.substr(0, n - 1) + "..." : str;
};

const Search = () => {
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const limit = 8; //    
  const [page, setPage] = useState(1);
  const params = useParams();

  /*
          
  62  currentPosts(data).map~  !!
    ..
  const indexOfLast = page * limit;
    const indexOfFirst = (page - 1) * limit;
    const currentPosts = (data) => {
        let currentPosts = 0;
        currentPosts = data.slice(indexOfFirst, indexOfLast);
        //console.log({"current post: ": currentPosts, "indexofFirst": indexOfFirst, "indexofLast": indexOfLast})
        return currentPosts;
      }; */

  const fetchData = async () => {
    try {
      //     error  users  
      setError(null);
      setData(null);
      // loading  true  .
      setLoading(true);
      const response = await axios.get(
        "/api/search/" + params.word + "/" + page
      );
      console.log(response.data);
      setData(response.data); //  response.data  .
    } catch (e) {
      setError(e);
    }
    setLoading(false);
  };

  useEffect(() => {
    fetchData();
  }, [page]);

  if (loading)
    return (
      <div>
        <Loading />
      </div>
    );
  if (error) return <div> </div>;
  if (!data) return null;
  return (
    <div>
      <div className={styles.itemlistcontent}>
        {data.map((item) => (
          <ListItem
            className={styles.listItem}
            key={item.name}
            id={item.id}
            market={item.market}
            price={item.price}
            name={truncate(item.name, 10)}
            image={item.image}
            hearts={item.hearts}
          />
        ))}
      </div>

      <div className={styles.pageMove}>
        <Pagination
          total={data.length}
          limit={limit}
          page={page}
          setPage={setPage}
        />
      </div>
    </div>
  );
};

export default Search;
